.App {
    text-align: center;
}
  
.Contact > div {
    color: #ffa260;
}
  
.load-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: 10;
    pointer-events: none;
}
  
.load-screen {
    position: relative;
    padding-top: 0px;
    padding-left: 0px;
    padding-right: 0px;
    background-color: #e4cb58;
    width: 0%;
    height: 100%;
}

.load-screen1 {
    position: relative;
    padding-top: 0px;
    padding-left: 0px;
    padding-right: 0px;
    background-color: #e4cb58;
    width: 100%;
    height: 0%;
}

.load-head {
    opacity: 0;
    pointer-events: none;
}